.contacts {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 400px; }

.contacts li {
  color: white;
  position: relative;
  font-size: 16px;
  font-family: "PT Mono", "Arial", monospace;
  line-height: 18px;
  margin-right: 120px; }
  .contacts li:last-of-type {
    margin-right: 0; }

.contacts__email::before {
  content: "";
  position: absolute;
  background-image: url("../img/envelope.svg");
  background-size: contain;
  background-repeat: no-repeat;
  width: 19px;
  height: 14px;
  left: -40px;
  top: 0; }

.contacts__phone::before {
  content: "";
  position: absolute;
  background-image: url("../img/phone-call.svg");
  background-size: contain;
  background-repeat: no-repeat;
  width: 18px;
  height: 18px;
  left: -30px;
  top: 0; }

.contacts a {
  text-decoration: none;
  color: inherit; }
  .contacts a:hover, .contacts a:focus {
    color: #00b2ff; }
  .contacts a:active {
    color: #00b2ff;
    opacity: 0.7; }

@media (max-width: 1023px) {
